export function readFeaturesToMismatches(readFeatures = [], start, qual) {
    const mismatches = new Array(readFeatures.length);
    let j = 0;
    let refPos = 0;
    let sublen = 0;
    let lastPos = start;
    let insertedBases = '';
    for (const ret of readFeatures) {
        const { refPos: p, code, pos, data, sub, ref } = ret;
        sublen = refPos - lastPos;
        lastPos = refPos;
        if (sublen && insertedBases.length > 0) {
            mismatches[j++] = {
                start: refPos,
                type: 'insertion',
                base: `${insertedBases.length}`,
                insertedBases,
                length: 0,
            };
            insertedBases = '';
        }
        refPos = p - 1 - start;
        if (code === 'X') {
            mismatches[j++] = {
                start: refPos,
                length: 1,
                base: sub,
                qual: qual === null || qual === void 0 ? void 0 : qual[pos - 1],
                altbase: ref === null || ref === void 0 ? void 0 : ref.toUpperCase(),
                type: 'mismatch',
            };
        }
        else if (code === 'I') {
            mismatches[j++] = {
                start: refPos,
                type: 'insertion',
                base: `${data.length}`,
                insertedBases: data,
                length: 0,
            };
        }
        else if (code === 'N') {
            mismatches[j++] = {
                type: 'skip',
                length: data,
                start: refPos,
                base: 'N',
            };
        }
        else if (code === 'S') {
            const len = data.length;
            mismatches[j++] = {
                start: refPos,
                type: 'softclip',
                base: `S${len}`,
                cliplen: len,
                length: 1,
            };
        }
        else if (code === 'P') {
        }
        else if (code === 'H') {
            const len = data;
            mismatches[j++] = {
                start: refPos,
                type: 'hardclip',
                base: `H${len}`,
                cliplen: len,
                length: 1,
            };
        }
        else if (code === 'D') {
            mismatches[j++] = {
                type: 'deletion',
                length: data,
                start: refPos,
                base: '*',
            };
        }
        else if (code === 'b') {
        }
        else if (code === 'q') {
        }
        else if (code === 'B') {
        }
        else if (code === 'i') {
            insertedBases += data;
        }
        else if (code === 'Q') {
        }
    }
    if (sublen && insertedBases.length > 0) {
        mismatches[j++] = {
            start: refPos,
            type: 'insertion',
            base: `${insertedBases.length}`,
            insertedBases,
            length: 0,
        };
        insertedBases = '';
    }
    return mismatches.slice(0, j);
}
